/**
 * Grab error message from control when validation fails.
 * @param control - control to search for errors on.
 * @param validation - validation performed on control.
 * @returns - error message if error exists, otherwise empty string.
 */
export var getErrorMessage = function (control, validation) {
    if (control.errorMessages[validation.errorType]) {
        return parseDynamicErrorMessage(control, validation);
    }
    if (control.errorMessages.default) {
        console.warn("Form Control: " + control.name + " does not have a error message for " + validation.errorType + ". Using default error message.");
        return control.errorMessages.default;
    }
    console.error("Form Control: " + control.name + " does not have a error message for " + validation.errorType + ". Need error message.");
    return '';
};
/**
 * Parses out a error message and replaces dynamic language.
 *
 * An error message can be like this:
 *
 * `The max length of this input is ::{maxLength}::`
 *
 * Whatever is enclosed inside the `::{}::` can be replaced by whatever the validation rule returns.
 *
 * So if the validation rule returns `{ errorType: 'maxLength', maxLength: 2, actualValue: 'asd' }`, any of the keys can replace dynamic language.
 *
 * So our error message goes from:
 *
 * `The max length of this input is ::{maxLength}::` to:
 *
 * `The max length of this input is 2` because we replaced the maxLength variable with the maxLength key from the validation rule.
 *
 * See the `IValidations` interface to see what each validation rule returns.
 * @param validation - The validation object.
 * @returns {string} The parsed error message.
 */
export var parseDynamicErrorMessage = function (control, validation) {
    if (!control.errorMessages.hasOwnProperty(validation.errorType)) {
        return '';
    }
    var errorMessage = control.errorMessages[validation.errorType] || '';
    var splitMessage = errorMessage.split('::');
    if (splitMessage.length > 1) {
        splitMessage
            .filter(function (e) { return /^{.*\}$/.test(e); })
            .map(function (e) {
            var key = e.replace(/{|}/g, '');
            return validation[key] !== undefined
                ? { variable: key, actual: validation[key] }
                : { variable: key, actual: key };
        })
            .forEach(function (e) {
            var replacer = '::{' + e.variable + '}::';
            var re = new RegExp(replacer, 'g');
            errorMessage = errorMessage.replace(re, e.actual);
        });
    }
    return errorMessage;
};

