import { createElement } from 'react';
import FileUploadComponent from '../InputComponents/FileUploadComponent';
import PhoneNumberComponent from '../InputComponents/PhoneNumberComponent';
// import RangeInputComponent from "../../FormControlInputComponents/RangeInputComponent/RangeInputComponent";
import NumberInputComponent from '../InputComponents/NumberInputComponent';
import TextAreaComponent from '../InputComponents/TextAreaComponent';
import InputComponent from '../InputComponents/InputComponent';
import SelectInputComponent from '../InputComponents/SelectInputComponent';
import { default as DatePickerComponent } from '../InputComponents/DatePickerComponent';
import PasswordComponent from '../InputComponents/PasswordComponent';
import { MultiInputComponent } from '../InputComponents/MultiInputComponent';
import NumberStringInputComponent from '../InputComponents/NumberStringInputComponent';
import CheckboxComponent from '../InputComponents/CheckboxComponent';
import CheckboxToggleComponent from '../InputComponents/CheckboxToggleComponent';
import CurrencyComponent from '../InputComponents/CurrencyComponent';
import SocialSecurityNumberComponent from '../InputComponents/SocialSecurityNumberComponent';
import MultiDataTypeComponent from '../InputComponents/MultiDataTypeComponent';
import { useFormContext } from '../../useHooks/useFormContext';
import RadioButtonComponent from '../InputComponents/RadioButtonComponent';
import SlideToggleComponent from '../InputComponents/SlideToggleComponent';
export function RXInput(_a) {
    var control = _a.control, isHidden = _a.isHidden;
    var formAsContext = useFormContext();
    var getComponentType = function (type) {
        switch (type) {
            case 'input':
                return InputComponent;
            case 'number':
                return NumberInputComponent;
            case 'phone':
            case 'tel':
                return PhoneNumberComponent;
            case 'select':
                return SelectInputComponent;
            case 'numberString':
                return NumberStringInputComponent;
            // case 'range':
            //     return RangeInputComponent;
            case 'currency':
                return CurrencyComponent;
            case 'checkbox':
                return CheckboxComponent;
            case 'checkboxToggle':
                return CheckboxToggleComponent;
            case 'radio':
                return RadioButtonComponent;
            case 'switch':
                return SlideToggleComponent;
            case 'textarea':
                return TextAreaComponent;
            case 'date':
                return DatePickerComponent;
            case 'file':
                return FileUploadComponent;
            case 'password':
                return PasswordComponent;
            case 'ssn':
                return SocialSecurityNumberComponent;
            case 'multi':
                return MultiInputComponent;
            case 'multiDataType':
                return MultiDataTypeComponent;
            default:
                return '';
        }
    };
    return createElement(getComponentType(control.type), {
        control: control,
        isHidden: isHidden,
        key: control.id,
        setIsBlurred: formAsContext.setIsBlurred,
        setIsTouched: formAsContext.setIsTouched,
        setIsDisabled: formAsContext.setIsDisabled,
        setValue: formAsContext.setValue
    });
}
RXInput.prototype.component = 'RXInput';

